.div {
    width: 400px;
    margin: 30px auto;
    background-color: #d5d6d2;
    color: #2f2e33;
    border-radius: 8px;
    box-shadow: 0px 0px 8px 6px rgba(199, 199, 199, 1);
    padding: 16px 0;
}

.message {
    margin-top: 20px;
    font-style: italic;
}

.heading {
    margin-bottom: 24px;
    text-align: center;
    font-weight: 500;
    font-size: 24px;
    color: #2f2e33;
}

.image{
    margin: 0 auto;
    width: 400px;
}